import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import CryptoExchangeScreen from "../screens/crypto-exchange";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const CryptoDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Cryptocurrency Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CryptoExchangeScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Cryptocurrency Exchange Development Company | Justtry Technologies"
    headTitle="Cryptocurrency Exchange Development Company | Justtry Technologies"
    description="justtrytech.com is a leading crypto exchange development company in India. They provide excellent  customized crypto exchange softwares with the industry's best crypto exchange developers."
    keywords="crypto exchange software solutions, cryptocurrency exchange software solution, crypto currency developers, crypto currency software, cryptocurrency software development, cryptocurrency software development company, cryptocurrency development solutions, crypto development company, cryptocurrency exchange development, crypto exchange platform development"
  ></Seo>
);

export default CryptoDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
